* {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  z-index: 500;
}


.directions {
  margin: 50px 0px 50px 0px;
  font-size: 24px;

}

h1 {
  margin: 0px;
  padding: 30px 0px 0px 0px;
  text-shadow: 3px 4px 5px black;
  text-decoration: none;
  font-size: 60px;
  z-index: 999;
}

header {
  height: 150px;
  width: 100%;
  top: 0;
  text-align: center;
  font-size: 125%;
  border-bottom: solid darkslategrey 1px;
  background-image: url('../../images/rotten-tomatillos.webp');
  background-size: cover;
  margin: 0 auto;
  position: fixed;
  color:bisque;
  box-shadow: inset 0px 0px 10px #282625;
  padding-bottom: 20px;
  z-index: 999;
}

main {
  padding-top: 170px;
  background-color: #e5d5c4;
  min-height: 100vh;
}

